const initialState = {
  isCreateNew:false,
}

export default function reducer(state=initialState, action){
  switch (action.type) {
    case "TOGGLE_NAME_PROJECT":{
      let bool = state.isCreateNew
      bool = !bool
      return {...state, isCreateNew:bool}
    }

  }
  return state
}



